import React, {useEffect, useState} from 'react';
import {platform, IOS, Group} from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Button from "@vkontakte/vkui/dist/es6/components/Button/Button";
import img1 from '../../img/img_room/29.png';
import img2 from '../../img/img_room/30.png';
import img3 from '../../img/img_room/31.png';
import img4 from '../../img/img_room/32.png';
import img5 from '../../img/img_room/33.png';
import img6 from '../../img/img_room/34.png';
import img7 from '../../img/img_room/35.png';
import img8 from '../../img/img_room/36.png';
import img10 from '../../img/img_room/37.png';
import img9 from '../../img/img_room/38.png';
import img1cat from '../../img/img_room_cat/29.png';
import img2cat from '../../img/img_room_cat/30.jpg';
import img3cat from '../../img/img_room_cat/31.png';
import img4cat from '../../img/img_room_cat/32.png';
import img5cat from '../../img/img_room_cat/33.png';
import img6cat from '../../img/img_room_cat/34.png';
import img7cat from '../../img/img_room_cat/35.png';
import img8cat from '../../img/img_room_cat/36.png';
import img9cat from '../../img/img_room_cat/37.png';
import img10cat from '../../img/img_room_cat/38.png';
import Alert from "@vkontakte/vkui/dist/components/Alert/Alert";

const Rooms04 = (props) => {
	const {getRandom, setActivePanel, alertResult, caughtСats, setСaughtСats, setResult, setPopout} = props;
	const rooms = [img1, img2, img3, img4, img5, img6, img7, img8, img9, img10]
	const roomsCat = [img1cat, img2cat, img3cat, img4cat, img5cat, img6cat, img7cat, img8cat, img9cat, img10cat]
	const [doorMumber, setDoorMumber]  = useState(getRandom(0,rooms.length))
	const [showAlert, setShowAlert] = useState(false);
	const [cats, setCats] = useState(false);
	const [message, setMessage] = useState("");
	const [catRan, setCatRan] = useState(false);
	let currentRooms = rooms[doorMumber]
	let currentRoomsCats = roomsCat[doorMumber]

	useEffect(() => {
		if (getRandom(0,2) === 0) {
			setCats(false)
		} else {
			setCats(true)
		}
	}, []);


	const getResultCloseRooms = () => {
		if (!catRan) {
			let random = getRandom(0,2)
			if (random === 0) {
				setActivePanel('ClosedDoor')
			} else {
				if (cats) {
					setActivePanel('ClosedDoor')
				} else {
					setCatRan(true)
					setCats(true)
					setMessage("Пока Вы шли к выходу, в комнату забежала кошечка!")
					setShowAlert(true)
				}
			}
		} else {
			setActivePanel('ClosedDoor')
		}
	}

	const catchСat = () => {
		if (cats) {
			let random = getRandom(0,2)
			setCats(false)
			if (random === 0) {
				setСaughtСats()
				setMessage("Вы поймали кошку!")
				setShowAlert(true)
			} else {
				setMessage("К сожалению, Вам не удалось поймать кошку!")
				setShowAlert(true)
			}
		} else {
			setMessage("После длительных поисков найти кошку не удалось")
			setShowAlert(true)
		}
	}

	const alert = (<Alert
			actions={[{
				title: 'Хорошо',
				autoclose: true,
				style: 'cancel'
			}]}
			onClose={() => {setShowAlert(false)}}
			className='alert'
		>
			<h2>{message}</h2>
		</Alert>
	)

	return 	<Panel id={props.id}>
		<Group className="rooms">
			{showAlert ? alert : ""}
			{cats ? <img src={currentRoomsCats} alt='rooms' className="img-rooms"/> : <img src={currentRooms} alt='rooms' className="img-rooms"/>}
			<Button className="button-close-rooms" onClick={
				() => {getResultCloseRooms()}
			} >Закрыть дверь комнаты</Button>
			<Button className="button-catch-cat" level="commerce" onClick={catchСat}>Поймать кошку</Button>
			<Button className="button-save-out" level="destructive" onClick={setResult}>Сохранить и выйти</Button>
		</Group>
	</Panel>
};


export default Rooms04;
